const NUMBER_LEVELS = ["", "", "", "", "  "];
const NUMBERS_LV1 = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];
const NUMBERS_LV2 = ["", "", "", "", "", "", "", ""];
const NUMBERS_LV3 = ["", "", "", "", "", "", " ", "", ""];

const numberChunkToStr = (num) => {
  const numArr = num
    .toString()
    .split("")
    .map((char) => parseInt(char));

  while (numArr.length < 3) {
    numArr.unshift(0);
  }

  if (numArr.length > 3) throw new Error("number must be under 1000 at number to subnumber");
  if (num === 0) return "";
  let tensChunk = "";
  const tensPart = Number(numArr.slice(1).join(""));

  if (tensPart < 20) tensChunk = NUMBERS_LV1[tensPart];
  else {
    if (numArr[2] === 0) tensChunk = NUMBERS_LV2[numArr[1] - 2];
    else tensChunk = NUMBERS_LV2[numArr[1] - 2] + "  " + NUMBERS_LV1[numArr[2]];
  }
  if (tensPart === 0) tensChunk = "";

  if (numArr[0] === 0) return tensChunk;
  if (tensPart === 0) return NUMBERS_LV3[numArr[0] - 1];
  return NUMBERS_LV3[numArr[0] - 1] + "  " + tensChunk;
};

const categorize = (bigNum) => {
  const categorized = bigNum.toLocaleString("en-US").split(",");
  console.log(bigNum);
  console.log(categorized);

  return categorized.map((item, idx) => {
    return {
      num: item,
      level: categorized.length - idx - 1,
    };
  });
};

export const convertNumberToString = (number) => {
  const sectenated = categorize(number);
  return sectenated
    .filter((item) => Number(item.num) !== 0)
    .map((item) => {
      return `${numberChunkToStr(Number(item.num))} ${NUMBER_LEVELS[item.level]}`;
    })
    .join("  ");
};
